    const { income } = this.state;
    const commaNumber = (x = 0) => {
      if (!x) return "0";
      x = Number(x);
      x = x.toFixed(0);
      x = x.toString();
      var lastThree = x.substring(x.length - 3);
      var otherNumbers = x.substring(0, x.length - 3);
      if (otherNumbers !== "") lastThree = "," + lastThree;
      var res = otherNumbers.replace(/\B(?=(\d{2})+(?!\d))/g, ",") + lastThree;
      return res;
    };
    const firstIncome = income > 50000 ? income - 50000 : income;
    const firstTaxAmount = {
      5:
        firstIncome > 250000 && firstIncome < 500000
          ? 0.05 * (firstIncome - 250000)
          : firstIncome > 500000
          ? 0.05 * 250000
          : 0,
      20:
        firstIncome > 500000 && firstIncome < 1000000
          ? 0.2 * (firstIncome - 500000)
          : firstIncome > 500000
          ? 0.2 * 500000
          : 0,
      30: firstIncome > 1000000 ? 0.3 * (firstIncome - 1000000) : 0,
    };
    let totalFirstTaxAmount = Object.values(firstTaxAmount).reduce(
      (a, b) => a + b,
      0
    );

